import React from "react";
import logo from '../img/logo.svg';
import '../css/index.css';
import {Link} from 'react-router-dom'
class Navbar extends React.Component{
    render(){
        return(
            <nav className="navbar navbar-expand-lg ">
  <a className="navbar-brand" href="#"><img className="imgl" src={logo}/> React</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav m-auto">
      <li className="nav-item">
          <Link exact  className="nav-link  active" to="/">Docs<span className="sr-only">(current)</span></Link>
      </li>
      <li className="nav-item">
        <Link exact  className="nav-link  " to="/Tutorial">Tutorial</Link>
      </li>
      <li className="nav-item">
        <Link exact  className="nav-link  " to="/Blog">Blog</Link>
      </li>
      
      <li className="nav-item">
      <Link exact  className="nav-link  " to="/Community">Community</Link>
      </li>
      
    </ul>
    <form className="form-inline ">
    <div class="input-group">
      <div class="input-group-prepend">
        <span class="input-group-text" id="basic-addon1"><i class="fas fa-search"></i></span>
      </div>
      <input type="text" class="srch-txt" placeholder="Search" aria-label="Search" aria-describedby="basic-addon1"/>
    </div>
    </form>
    <ul className="navbar-nav ">
    <li className="nav-item">
        <a className="nav-link " href="#">V 17.0.2</a>
      </li>
      <li className="nav-item">
        <a className="nav-link " href="#"><i class="fas fa-language" ></i> Language</a>
      </li>
      <li className="nav-item">
        <a className="nav-link " href="#">Git <i class="fas fa-external-link-alt"></i></a>
      </li>
    </ul>
  </div>
</nav>
        )
    }

}
export default Navbar;