import './App.css';
import Navbar from './Static/comp/Navbar';
import{BrowserRouter,Route,Switch} from 'react-router-dom';
import Home from './Static/comp/Home';
import Blog from './Static/comp/Blog';
import Tutorial from './Static/comp/Tutorial';
import Community from './Static/comp/Community';
import { Component } from 'react';
import Footer from './Static/comp/Footer';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
      <Navbar />
      <Switch>
      <Route exact path="/" component={Home}/>
      <Route exact path="/Blog" component={Blog}/>
      <Route exact path="/Tutorial" component={Tutorial}/>
      <Route exact path="/Community" component={Community}/>
      </Switch>
      <Footer/>
      </BrowserRouter>
      
    </div>
  );
}

export default App;
